body {
  background-color: #7ab5d1;
}
.App {
  background-color: #3c6177;
  min-height: 800px;
  width: 500px;
  margin: 100px auto;
  border-radius: 20px;
}

.click:hover {
  background: #40e0d0;
  transform: scale(1.1);
}

#to-do-form input {
  background-color: rgba(27, 112, 137, 1);
  border: 0;
  color: #fff;
  width: 320px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}
#to-do-form input:focus {
  outline: none;
}
#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
}
#to-do-form button:focus {
  outline: none;
}
